import orderBy from '@/utils/orderBy';
export default {
  mixins: [orderBy],
  data: function data() {
    return {
      title: '',
      username: '',
      userId: '',
      userList: [],
      textarea: '',
      currentRow: '',
      isShowAdd: false,
      addInfo: {
        customer_id: '',
        nickname: '',
        account: '',
        password: '',
        reg_mobile: '',
        is_valid: '1',
        remarks: '',
        face_img: '',
        rid: []
      },
      temp: {
        customer_id: '',
        nickname: '',
        account: '',
        password: '',
        reg_mobile: '',
        is_valid: '1',
        remarks: '',
        face_img: '',
        rid: []
      },
      imageUrl: '',
      formdate: '',
      rules: {
        customer_id: [{
          required: true,
          message: '请输入用户ID',
          trigger: 'blur'
        }],
        nickname: [{
          required: true,
          message: '请输入用户名称',
          trigger: 'blur'
        }],
        account: [{
          required: true,
          message: '请输入账户',
          trigger: 'blur'
        }],
        password: [{
          required: true,
