import request from '@/utils/request';
export function adminlist(data) {
  return request({
    url: 'app/setting/admin/list',
    method: 'post',
    data: data
  });
}
export function adminlistAdmin(data) {
  return request({
    url: 'app/setting/admin/listAdmin',
    method: 'post',
    data: data
  });
}
export function adduser(data) {
  return request({
    url: 'app/setting/admin/add',
    method: 'post',
    data: data
  });
}
export function updateValid(data) {
  return request({
    url: 'app/setting/admin/updateValid',
    method: 'post',
    data: data
  });
}
export function setpassword(data) {
  return request({
    url: 'app/setting/admin/reset-password',
    method: 'post',
    data: data
  });
}
export function deleteuser(data) {
  return request({
    url: 'app/setting/admin/delete',
    method: 'post',
    data: data
  });
}
export function uploadpic(fromdata) {
  return request({
    url: 'app/file/upload',
    method: 'post',
    fromdata: fromdata,
    data: {},
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
}
export function rolelist(data) {
  return request({
    url: 'app/setting/role/list',
    method: 'post',
    data: data
  });
}
export function clientlist(data) {
  return request({
    url: 'app/setting/customer/list',
    method: 'post',
    data: data
  });
}